
import { defineComponent, onMounted, ref } from "vue";

import {
  IonButtons,
  IonContent,
  IonHeader,
  IonMenuButton,
  IonPage,
  IonTitle,
  IonToolbar,
  IonCard,
  IonCardHeader,
  IonCardContent,
  IonCardSubtitle,
  IonCardTitle,
  IonItem,
  IonLabel,
  IonList,
  IonButton,
  IonInput,
} from "@ionic/vue";

import { useStore, store, ACTIONS } from "../store";

export default defineComponent({
  name: "Home",
  components: {
    IonButtons,
    IonContent,
    IonHeader,
    IonMenuButton,
    IonPage,
    IonTitle,
    IonToolbar,
    IonCard,
    IonCardHeader,
    IonCardContent,
    IonCardSubtitle,
    IonCardTitle,
    IonItem,
    IonLabel,
    IonList,
    IonButton,
    IonInput,
  },
  setup() {
    const store = useStore();
    const dinnerCode = ref("");
    const dinnerCodeError = ref("");
    const joinDinner = () => {
      store.dispatch(ACTIONS.SET_STATE_ID, dinnerCode.value).
      then(()=> dinnerCodeError.value = "")
      .catch(error => {
          console.log(error);
          dinnerCodeError.value="unbekannter Dinner Code"});
    };
    return { store, dinnerCode, dinnerCodeError, joinDinner };
  },
});
